@media all and (min-width: 480px) {
  .login {
      padding: 60px 0;
  }

  .login form {
      max-width: 420px;
  }
}

h1 {
  text-shadow: 1px 1px 1px black;
}

span {
  color: red;
  font-weight: bold;
}

.Submit {
  background-color: #007BFF;
  Color: white;
  font-weight: bold;
  padding: 10px 40px;
  border-radius: 3px;
  cursor: pointer;
  box-shadow: 0 8px 16px 0 grey;
  text-decoration: none;
}

.visible {
  padding-top: 2em;
  color: red;
}

.cache {
  visibility: hidden;
}